import React from "react";
import styles from "./checkItem.module.css";
import DATABASE from "../../localData/imageData.json";

const CheckItem = ({ item, addItem, deleteItem }) => {
  const { name, category } = DATABASE[item];
  let type;
  switch (category) {
    case "Necklace":
      type = "checkbox";
      break;
    default:
      type = "radio";
  }
  const onCheckBox = (event) => {
    const target = event.target;
    if (target.checked) {
      addItem(category, target.dataset.item);
      return;
    }
    deleteItem(category, target.dataset.item);
  };
  return (
    <div className={styles.checkbox}>
      <input
        type={type}
        id={name}
        name={category}
        data-item={item}
        onChange={onCheckBox}
      />
      <label className={styles.label} htmlFor={name}>
        {name}
      </label>
    </div>
  );
};

export default CheckItem;
