import Header from "./components/header/header";
import Tweets from "./components/tweet/Tweets";
import { Route, HashRouter } from "react-router-dom";
import Icecream from "./components/icecream/Icecream";
import styles from "./App.module.css";
import ReactGa from "react-ga";
import { useEffect } from "react";
import Payment from "./components/payment/Payment";
import { Elements } from "@stripe/react-stripe-js";
import Gallery from "./components/gallery/gallery";
import About from "./components/about/about";
import Contact from "./components/contact/contact";
import Pricing from "./components/pricing/Pricing";
import Footer from "./components/footer/Footer";
import Login from "./components/login/Login";

function App({ twitter, stripePromise, cloudinary, database }) {
  useEffect(() => {
    ReactGa.initialize("UA-208921762-2");

    //to report page viewss
    ReactGa.pageview(window.location.pathname + window.location.search);
  }, []);

  return (
    <div className={styles.app}>
      <div className={styles.content}>
        <HashRouter>
          <nav className={styles.header}>
            <Header />
          </nav>
          <Route exact path="/about">
            <About />
          </Route>
          <Route exact path={["/", "/gallery"]}>
            <Gallery database={database} />
          </Route>
          <Route path="/tweets">
            <Tweets twitter={twitter} />
          </Route>
          <Route path="/icecream">
            <Icecream stripePromise={stripePromise} />
          </Route>
          <Route exact path="/payment">
            <Elements stripe={stripePromise}>
              <Payment />
            </Elements>
          </Route>
          <Route exact path="/pricing">
            <Pricing />
          </Route>
          <Route exact path="/contact">
            <Contact cloudinary={cloudinary} database={database} />
          </Route>
          <Route exact path="/login">
            <Login />
          </Route>
        </HashRouter>
      </div>
      <footer className={styles.footer}>
        <Footer />
      </footer>
    </div>
  );
}

export default App;
