export default class Twitter {
  async getTweets(checkedNameList, searchKeyword) {
    //checkedNameList = ["beeple", "mcuban", "pplpleasr1"...]

    const query = checkedNameList.join(",");
    const response = await fetch(
      `${process.env.REACT_APP_TWEET_FETCH_URL}/tweets?authors=${query}&keyword=${searchKeyword}`
    );
    const jsonResponse = await response.json();
    if (!jsonResponse.includes) {
      return undefined;
    }

    const users = {};
    const authorData = jsonResponse.includes.users;
    authorData.forEach((user) => {
      users[user.id] = { name: user.name, username: user.username };
    });

    return { tweets: jsonResponse.data, users };
  }
}
