import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import React, { useEffect, useRef, useState } from "react";
import styles from "./payment.module.css";
const Payment = ({ plan, setIsPaymentComplete }) => {
  const [succeeded, setSucceeded] = useState(false);
  const [error, setError] = useState(null);
  const [processing, setProcessing] = useState("");
  const [disabled, setDisabled] = useState(true);
  const [clientSecret, setClientSecret] = useState("");
  const [email, setEmail] = useState("");

  const stripe = useStripe();
  const elements = useElements();
  const planRef = useRef();

  const amount = plan && plan.replace("$", "");

  useEffect(() => {
    amount &&
      fetch(
        `${process.env.REACT_APP_TWEET_FETCH_URL}/payment/create-payment-intent`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ amount }),
        }
      ) //
        .then((res) => {
          return res.json();
        }) //
        .then((data) => {
          setClientSecret(data.clientSecret);
        });
  }, [amount]);

  const cardStyle = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: "Arial, sans-serif",
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#32325d",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };
  const handleSubmit = async (event) => {
    event.preventDefault();
    setProcessing(true);

    const payload = await stripe.confirmCardPayment(clientSecret, {
      receipt_email: email,
      payment_method: {
        card: elements.getElement(CardElement),
      },
    });
    if (payload.error) {
      setError(`Payment failed ${payload.error.message}`);
      setProcessing(false);
    } else {
      setError(null);
      setProcessing(false);
      setSucceeded(true);
      console.log("Payment Successful!");
      setIsPaymentComplete(true);
    }
  };
  const handleChange = async (event) => {
    setDisabled(event.empty);
    setError(event.error ? event.error.message : "");
  };

  return (
    <form id="payment-form" onSubmit={handleSubmit} className={styles.form}>
      <div ref={planRef} className={`${styles.input} ${styles.plan}`}>
        {plan ? plan : "Select a plan"}
      </div>
      <input
        className={styles.input}
        type="email"
        onChange={(e) => setEmail(e.target.value)}
        placeholder="Enter email address"
      />
      <div className={styles.stripe}>
        <CardElement
          id={styles.cardElement}
          onChange={handleChange}
          options={cardStyle}
          className={styles.cardInput}
        />
        <button
          disabled={processing || disabled || succeeded}
          id="submit"
          className={styles.button}
        >
          <span id="button-text">
            {processing ? (
              <div className={styles.spinner} id="spinner"></div>
            ) : (
              "Pay now"
            )}
          </span>
        </button>
        {/* Show any error that happens when processing the payment */}
        {error && (
          <p className={styles.cardError} role="alert">
            {error}
          </p>
        )}
        {/* Show a success message upon completion */}
        <p
          className={
            succeeded
              ? styles.resultMessage
              : `${styles.resultMessage} ${styles.hidden}`
          }
        >
          Payment succeeded, Refresh the page to draw new one again.
        </p>
      </div>
    </form>
  );
};

export default Payment;
