import React from "react";
import styles from "./tweet.module.css";

const Tweet = ({ data, users }) => {
  const utcDate = data["created_at"];
  const localDate = new Date(utcDate).toString();

  return (
    <div className={styles.container}>
      <a
        className={styles.link}
        target="_blank"
        rel="noreferrer"
        href={`https://twitter.com/${
          users[data.author_id]["username"]
        }/status/${data.id}`}
      >
        <div className={styles.profileBox}>
          {/* <img
            src="https://res.cloudinary.com/db7ss52zt/image/upload/v1632529994/Mint-Color-solid-color-background_bvbi4z.jpg"
            alt="profile-pic"
            className={styles.picture}
          /> */}
          <div className={styles.nameUsernameContainer}>
            <div className={styles.name}>{users[data.author_id]["name"]}</div>
            <div className={styles.username}>
              {`@${users[data.author_id]["username"]}`}
            </div>
          </div>
        </div>
        <div className={styles.textBox}>
          <span className={styles.text}>{data.text}</span>
        </div>
        <div className={styles.countBox}>
          <div className={styles.retweetBox}>
            Retweet Count
            <div className={styles.retweetCount}>
              {data.public_metrics.retweet_count}
            </div>
          </div>
          <div className={styles.replyBox}>
            Reply Count
            <div className={styles.replyCount}>
              {data.public_metrics.reply_count}
            </div>
          </div>
          <div className={styles.likeBox}>
            Like Count
            <div className={styles.likeCount}>
              {data.public_metrics.like_count}
            </div>
          </div>
          <div className={styles.quoteBox}>
            Quote Count
            <div className={styles.quoteCount}>
              {data.public_metrics.quote_count}
            </div>
          </div>
        </div>
        <div className={styles.createdAt}>{localDate}</div>
      </a>
    </div>
  );
};
export default Tweet;
