import React, { useEffect, useState } from "react";
import GalleryCard from "../galleryCard/GalleryCard";
import Adsense from "../googld-ad/Adsense";
import styles from "./gallery.module.css";

const Gallery = ({ database }) => {
  const [nftData, setNftData] = useState();

  useEffect(() => {
    database.getNftData((data) => {
      const nftDataKeys = Object.keys(data);
      setNftData({ nftDataKeys, data });
    });
  });
  return (
    <div>
      <div className={styles.container}>
        {nftData &&
          nftData.nftDataKeys.map((key) => (
            <GalleryCard
              key={key}
              id={key}
              database={database}
              data={nftData.data[key]}
            />
          ))}
      </div>
      <Adsense />
    </div>
  );
};

export default Gallery;
