import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import styles from "./header.module.css";

const Header = () => {
  const [isNavBarClicked, setIsNavBarClicked] = useState(true);

  const onClickHandler = (event) => {
    isNavBarClicked ? setIsNavBarClicked(false) : setIsNavBarClicked(true);
  };
  return (
    <header className={styles.header}>
      <NavLink to="/" exact className={styles.logo}>
        ICECREAMPUNKS
      </NavLink>
      <div className={styles.navBar} onClick={onClickHandler}>
        <i className="fas fa-bars"></i>
      </div>
      <div
        className={`${styles.navContainer} ${
          isNavBarClicked ? null : styles.hidden
        }`}
      >
        <NavLink
          to="/about"
          activeClassName={styles.active}
          className={styles.aTag}
        >
          About
        </NavLink>
        <NavLink
          to="/tweets"
          activeClassName={styles.active}
          className={styles.aTag}
        >
          NFT Tweets
        </NavLink>
        <NavLink
          to="/icecream"
          activeClassName={styles.active}
          className={styles.aTag}
        >
          Icecreampunks
        </NavLink>
        <NavLink
          to="/"
          exact
          activeClassName={styles.active}
          className={styles.aTag}
        >
          Gallery
        </NavLink>
        <NavLink
          to="/pricing"
          activeClassName={styles.active}
          className={styles.aTag}
        >
          Pricing
        </NavLink>
        <NavLink
          to="/contact"
          activeClassName={styles.active}
          className={styles.aTag}
        >
          Contact
        </NavLink>
      </div>
    </header>
  );
};

export default Header;
