// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getDatabase, ref, set, update, get, child } from "firebase/database";

// Set the configuration for your app
// TODO: Replace with your project's config object
export default class FirebaseDatabase {
  constructor() {
    const firebaseConfig = {
      apiKey: "AIzaSyDb4ncsxaXsAa8KueT77Eji4BR3qqetDkk",
      authDomain: "icecreampunks.firebaseapp.com",
      databaseURL: "https://icecreampunks-default-rtdb.firebaseio.com",
      storageBucket: "icecreampunks.appspot.com",
    };
    const app = initializeApp(firebaseConfig);
    this.database = getDatabase(app);
  }
  writeNftData(id, name, desc, imageUrl, likeCount, contractAddress, tokenId) {
    set(ref(this.database, "nfts/" + id), {
      name,
      desc,
      src: imageUrl,
      likeCount,
      contractAddress,
      tokenId,
    });
  }
  updateNftLikeCount(id, likeCount) {
    update(ref(this.database, "nfts/" + id), { likeCount });
  }
  getNftData(callbackfn) {
    const nftDbRef = ref(this.database);
    get(child(nftDbRef, "nfts/"))
      .then((snapshot) => {
        if (snapshot.exists()) {
          callbackfn(snapshot.val());
        } else {
          console.log("No data available");
        }
      })
      .catch((error) => {
        console.error(error);
      });
  }
  writeRequestData(firstName, lastName, email, phone, photoURL, message) {
    set(ref(this.database, "requests/" + Date.now()), {
      firstName,
      lastName,
      email,
      phone,
      photoURL,
      message,
    });
  }
}
