import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import Twitter from "./services/twitter/twitter";
import { loadStripe } from "@stripe/stripe-js";
import Cloudinary from "./services/cloudinary/cloudinary";
import FirebaseDatabase from "./services/firebase/realtimeDatabase";
const twitter = new Twitter();
const promise = loadStripe(process.env.REACT_APP_STRIPE_PUBLIC_KEY);
const cloudinary = new Cloudinary();
const database = new FirebaseDatabase();
ReactDOM.render(
  <React.StrictMode>
    <App
      twitter={twitter}
      stripePromise={promise}
      cloudinary={cloudinary}
      database={database}
    />
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
