import React, { useRef, useState } from "react";
import { Spinner } from "react-bootstrap";
import Button from "../button/button";
import styles from "./contact.module.css";

const Contact = ({ cloudinary, database }) => {
  const fileInputRef = useRef();
  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [photoFile, setPhotoFile] = useState();
  const [message, setMessage] = useState();
  const [imgURL, setImgURL] = useState();
  const [isSubmitted, setIsSumitted] = useState();
  const [progress, setProgress] = useState();

  const submitHandler = async (event) => {
    event.preventDefault();
    setProgress("on");
    const result = await cloudinary.imageUpload(photoFile, [
      firstName,
      lastName,
      email,
      phone,
      message,
    ]);

    const imgCloudURL = result.url;
    database.writeRequestData(
      firstName,
      lastName,
      email,
      phone,
      imgCloudURL,
      message
    );
    setIsSumitted(true);
    setProgress(undefined);
  };

  const createImageURL = (file) => {
    if (!file) {
      setImgURL("");
      return;
    }
    setImgURL(URL.createObjectURL(file));
  };

  const handleInputChange = (event) => {
    const target = event.target;
    switch (target.name) {
      case "firstName":
        setFirstName(target.value);
        break;
      case "lastName":
        setLastName(target.value);
        break;
      case "email":
        setEmail(target.value);
        break;
      case "phone":
        setPhone(target.value);
        break;
      case "file":
        const file = fileInputRef.current.files[0];
        createImageURL(file);
        setPhotoFile(file);
        break;
      case "message":
        setMessage(target.value);
        break;
      default:
        throw new Error("Unsupported Input");
    }
  };
  return (
    <div className={styles.container}>
      {isSubmitted ? (
        "Submitted"
      ) : (
        <>
          <form className={styles.form} onSubmit={submitHandler}>
            <fieldset>
              <legend>Contact Our Design Team</legend>
              <div className={styles.inputContainer}>
                <div className={styles.firstName}>
                  <label htmlFor="firstName" className={styles.label}>
                    First name<span className={styles.required}>*</span>
                  </label>
                  <input
                    type="text"
                    id="firstName"
                    name="firstName"
                    placeholder="First Name"
                    onChange={handleInputChange}
                    required
                    className={styles.inputBox}
                  />
                </div>
                <div className={styles.lastName}>
                  <label htmlFor="lastName" className={styles.label}>
                    Last name<span className={styles.required}>*</span>
                  </label>
                  <input
                    type="text"
                    id="lastName"
                    name="lastName"
                    placeholder="Last Name"
                    onChange={handleInputChange}
                    required
                    className={styles.inputBox}
                  />
                </div>
                <div className={styles.email}>
                  <label htmlFor="email" className={styles.label}>
                    Email<span className={styles.required}>*</span>
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    placeholder="email@example.com"
                    onChange={handleInputChange}
                    required
                    className={styles.inputBox}
                  />
                </div>
                <div className={styles.phone}>
                  <label htmlFor="phone" className={styles.label}>
                    Phone<span className={styles.required}>*</span>
                  </label>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    placeholder="+5 (555) 555-5555"
                    onChange={handleInputChange}
                    required
                    className={styles.inputBox}
                  />
                </div>
                <div className={styles.file}>
                  <label htmlFor="file" className={styles.label}>
                    Choose your photo<span className={styles.required}>*</span>
                  </label>
                  <input
                    type="file"
                    accept="image/*"
                    id="file"
                    name="file"
                    ref={fileInputRef}
                    onChange={handleInputChange}
                    required
                    className={`${styles.chooseFile} ${styles.inputBox}`}
                  />
                </div>
                <div className={styles.message}>
                  <label htmlFor="message" className={styles.label}>
                    Message<span className={styles.required}>*</span>
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    placeholder="Tell us more about your needs"
                    onChange={handleInputChange}
                    required
                    className={`${styles.textarea} ${styles.inputBox}`}
                  />
                </div>
                {progress === "on" ? (
                  <Spinner
                    className={styles.spinner}
                    animation="grow"
                    variant="light"
                  />
                ) : (
                  <Button type="submit" text={"Submit"} />
                )}
              </div>
            </fieldset>
          </form>
          {imgURL && (
            <img
              className={styles.uploadedImg}
              src={imgURL}
              alt="Uploaded_IMG"
              width="300px"
              height="300px"
            />
          )}
        </>
      )}
    </div>
  );
};

export default Contact;
