import React from "react";
import PricingDetails from "../pricingDetails/PricingDetails";
import styles from "./pricing.module.css";
import Button from "../button/button";
import { Link } from "react-router-dom";

function Pricing(props) {
  return (
    <div className={styles.container}>
      <div className={styles.pricingCard}>
        <div className={styles.planContainer}>
          <div className={styles.planName}>FREE</div>
          <div className={styles.planPrice}>$0</div>
          <Link to="/icecream">
            <Button text={"Go"} />
          </Link>
        </div>
        <div className={styles.planContainer}>
          <div className={styles.planName}>BASIC</div>
          <div className={styles.planPrice}>$1.99</div>
          <Link to="/icecream">
            <Button text={"Go"} />
          </Link>
        </div>
        <div className={styles.planContainer}>
          <div className={styles.planName}>CUSTOM</div>
          <div className={styles.planPrice}>$19.99</div>
          <Link to="/contact">
            <Button text={"Contact Us"} />
          </Link>
        </div>
        <div className={styles.planContainer}>
          <div className={styles.planName}>PREMIER</div>
          <div className={styles.planPrice}>$199.99</div>
          <Link to="/contact">
            <Button text={"Contact Us"} />
          </Link>
        </div>
      </div>
      <div className={styles.planDetailContainer}>
        <PricingDetails />
      </div>
    </div>
  );
}

export default Pricing;
