import React, { useState } from "react";
import styles from "./galleryCard.module.css";

const GalleryCard = ({ id, data, database }) => {
  const [isLiked, setIsLiked] = useState(false);
  const likeClickHandler = (event) => {
    if (isLiked) {
      setIsLiked(false);
      data.likeCount--;
      database.updateNftLikeCount(id, data.likeCount);
    } else {
      setIsLiked(true);
      data.likeCount++;
      database.updateNftLikeCount(id, data.likeCount);
    }
  };
  return (
    <div className={styles.card}>
      <div className={styles.header}>
        <div className={styles.likeButton} onClick={likeClickHandler}>
          {isLiked ? (
            <i className={`fas fa-heart ${styles.filledLikeIcon}`}></i>
          ) : (
            <i className={`far fa-heart ${styles.emptyLikeIcon}`}></i>
          )}
        </div>

        <span className={styles.likeCount}>{data.likeCount}</span>
      </div>
      <a
        className={styles.aTag}
        rel="noreferrer"
        target="_blank"
        href={`https://opensea.io/assets/${data.contractAddress}/${data.tokenId}`}
      >
        <div className={styles.imageContainer}>
          <img className={styles.image} src={data.src} alt={data.name} />
        </div>
        <div className={styles.footer}>
          <span className={styles.name}>{data.name}</span>
          <span className={styles.desc}>{data.desc}</span>
        </div>
      </a>
    </div>
  );
};

export default GalleryCard;
