import { useEffect, useRef, useState } from "react";
import CheckItem from "../check-item/checkItem";
import imageData from "../../localData/imageData.json";
import styles from "./icecream.module.css";
import Button from "../button/button";
import ReactGa from "react-ga";
import Adsense from "../googld-ad/Adsense";
import { Elements } from "@stripe/react-stripe-js";
import Payment from "../payment/Payment";

function Icecream({ stripePromise }) {
  const baseCanvasRef = useRef();
  const backgroundCanvasRef = useRef();
  const bodyCanvasRef = useRef();
  const skinCanvasRef = useRef();
  const hairstyleCanvasRef = useRef();
  const eyeCanvasRef = useRef();
  const mouthCanvasRef = useRef();
  const clothesCanvasRef = useRef();
  const necklaceCanvasRef = useRef();
  const icecreamCanvasRef = useRef();
  const watermarkCanvasRef = useRef();
  const downloadRef = useRef();

  const [canvasImage, setCanvasImage] = useState({
    Background: "",
    Body: "body",
    Skin: "",
    Hairstyle: "",
    Eye: "",
    Mouth: "",
    Clothes: "",
    Necklace: "",
    Icecream: "",
  });
  const [plan, setPlan] = useState();
  const [isPaymentComplete, setIsPaymentComplete] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState();
  const [active, setActive] = useState();

  const canvasRefArray = [
    backgroundCanvasRef,
    bodyCanvasRef,
    skinCanvasRef,
    hairstyleCanvasRef,
    eyeCanvasRef,
    mouthCanvasRef,
    clothesCanvasRef,
    necklaceCanvasRef,
    icecreamCanvasRef,
  ];

  function drawOnCanvas(canvasRef, item) {
    if (!item) {
      const canvas = canvasRef.current;
      canvas.oncontextmenu = () => false;
      const ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      return;
    }
    const canvas = canvasRef.current;
    canvas.oncontextmenu = () => false;
    const ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    const img = new Image();
    img.src = imageData[item].src;
    img.crossOrigin = "Anonymous";
    img.onload = () => {
      ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
    };
  }

  function bindCanvas(baseCanvasRef, canvasRefArray) {
    const baseCanvas = baseCanvasRef.current;
    const baseCtx = baseCanvas.getContext("2d");
    baseCtx.clearRect(0, 0, baseCanvas.width, baseCanvas.height);
    canvasRefArray &&
      canvasRefArray.forEach((canvasRef) => {
        baseCtx.drawImage(canvasRef.current, 0, 0);
      });
    const resultURL = baseCanvas.toDataURL("image/png");
    return resultURL;
  }

  function drawWatermark(watermarkCanvasRef, watermarkText) {
    const watermarkCanvas = watermarkCanvasRef.current;
    watermarkCanvas.oncontextmenu = () => false;
    const watermarkCtx = watermarkCanvas.getContext("2d");
    watermarkCtx.clearRect(0, 0, watermarkCanvas.width, watermarkCanvas.height);
    watermarkCtx.font = "bold 126px Comic Neue";
    watermarkCtx.fillStyle = "red";
    watermarkCtx.textBaseline = "top";
    for (
      let h = watermarkCanvas.width / 50;
      h < watermarkCanvas.width;
      h = h + (watermarkCanvas.width * 2) / 5
    ) {
      for (
        let w = watermarkCanvas.width / 50;
        w < watermarkCanvas.width;
        w = w + (watermarkCanvas.width * 1.05) / 2
      ) {
        watermarkCtx.fillText(watermarkText, w, h);
      }
    }
  }

  function removeWatermark(watermarkCanvasRef) {
    const watermarkCanvas = watermarkCanvasRef.current;
    watermarkCanvas.oncontextmenu = () => false;
    const watermarkCtx = watermarkCanvas.getContext("2d");
    watermarkCtx.clearRect(0, 0, watermarkCanvas.width, watermarkCanvas.height);
  }

  useEffect(() => {
    drawOnCanvas(backgroundCanvasRef, canvasImage.Background);
    drawOnCanvas(bodyCanvasRef, canvasImage.Body);
    drawOnCanvas(skinCanvasRef, canvasImage.Skin);
    drawOnCanvas(hairstyleCanvasRef, canvasImage.Hairstyle);
    drawOnCanvas(eyeCanvasRef, canvasImage.Eye);
    drawOnCanvas(mouthCanvasRef, canvasImage.Mouth);
    drawOnCanvas(clothesCanvasRef, canvasImage.Clothes);
    drawOnCanvas(necklaceCanvasRef, canvasImage.Necklace);
    drawOnCanvas(icecreamCanvasRef, canvasImage.Icecream);
    drawWatermark(watermarkCanvasRef, "Icecreampunks!");
    isPaymentComplete && removeWatermark(watermarkCanvasRef);
  });

  function addItem(category, item) {
    const copiedCanvasImage = { ...canvasImage };
    copiedCanvasImage[category] = item;
    setCanvasImage(copiedCanvasImage);
  }

  function deleteItem(category, item) {
    const copiedCanvasImage = { ...canvasImage };
    copiedCanvasImage[category] = "";
    setCanvasImage(copiedCanvasImage);
  }

  const totalItems = Object.keys(imageData);

  async function shareWebsite() {
    ReactGa.event({
      category: "Share Button",
      action: "Click Share button from Icecreampunks page",
    });

    const imageUrl = bindCanvas(
      baseCanvasRef,
      isPaymentComplete
        ? canvasRefArray
        : [...canvasRefArray, watermarkCanvasRef]
    );

    const blob = await (await fetch(imageUrl)).blob();
    const filesArray = [
      new File([blob], "Icecreampunk.png", {
        type: blob.type,
        lastModified: new Date().getTime(),
      }),
    ];
    if (navigator.canShare && navigator.canShare({ files: filesArray })) {
      navigator
        .share({
          title: "Icecreampunks",
          text: "Create Your Own Icecreampunks",
          url: window.location.href,
          files: filesArray,
        })
        .then(() => {})
        .catch(console.error);
    } else {
      console.log("not supported!");
    }
  }
  const categories = [
    "Background",
    "Skin",
    "Hairstyle",
    "Eye",
    // "Eyebrows",
    "Mouth",
    // "Facial hair",
    "Clothes",
    "Necklace",
    "Icecream",
    // "Etc",
  ];

  const clickHandler = () => {
    ReactGa.event({
      category: "Download Button",
      action: "Click Download button from Icecreampunks page",
    });
    const url = bindCanvas(baseCanvasRef, canvasRefArray);
    downloadRef.current.href = url;
    downloadRef.current.click();
  };

  const handleInputChange = (event) => {
    const target = event.target;
    setPlan(target.value);
  };

  const categoryOnclickHandler = (event) => {
    const categoryName = event.target.innerText;
    setSelectedCategory(categoryName);
    setActive(categoryName);
  };

  return (
    <div className={styles.bodyContainer}>
      <div className={styles.body}>
        <div className={styles.category}>
          <div className={styles.categoryNavContainer}>
            {categories.map((category) => (
              <div
                className={`${styles.categoryBox} ${
                  active === category ? styles.active : ""
                }`}
                onClick={categoryOnclickHandler}
                key={category}
              >
                {category}
              </div>
            ))}
          </div>
          {selectedCategory && (
            <div className={styles.checkItemContainer}>
              {selectedCategory &&
                totalItems
                  .filter(
                    (item) => imageData[item]["category"] === selectedCategory
                  )
                  .map((item) => (
                    <CheckItem
                      key={item}
                      item={item}
                      addItem={addItem}
                      deleteItem={deleteItem}
                    />
                  ))}
            </div>
          )}
        </div>
        <div className={styles.canvasWrapper}>
          <div className={styles.canvasContainer}>
            <canvas
              ref={baseCanvasRef}
              className={styles.baseCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={backgroundCanvasRef}
              className={styles.backgroundCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={bodyCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={skinCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={hairstyleCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={eyeCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={mouthCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={clothesCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={necklaceCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={icecreamCanvasRef}
              className={styles.bodyCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
            <canvas
              ref={watermarkCanvasRef}
              className={styles.watermarkCanvas}
              width="1968px"
              height="1968px"
            ></canvas>
          </div>

          <span className={styles.download}>
            Preview <i className="fas fa-ice-cream"></i>
          </span>

          <Button text={"SHARE"} onClick={shareWebsite} />
          {isPaymentComplete && (
            <>
              <button className={styles.downloadButton} onClick={clickHandler}>
                Download Image
              </button>
              <a
                href="/icecream"
                ref={downloadRef}
                className={styles.hidden}
                download="Icecreampunk.png"
              >
                Download
              </a>
            </>
          )}
          <div className={styles.payment}>
            <div className={styles.text}>
              Activate Lossless Image Download Without Watermark
            </div>
            <div className={styles.text2}>(Only for $1.99)</div>
            <form className={styles.planContainer}>
              <div className={styles.plan}>
                <input
                  type="radio"
                  name="plan"
                  id="1.99"
                  value="$1.99"
                  onChange={handleInputChange}
                  className={styles.valid}
                />
                <label className={styles.valid} htmlFor="1.99">
                  $1.99
                </label>
              </div>
              <div className={styles.plan}>
                <input
                  type="radio"
                  name="plan"
                  id="9.99"
                  value="$9.99"
                  onChange={handleInputChange}
                  disabled
                />
                <label htmlFor="9.99">
                  <strike>$9.99</strike>
                </label>
              </div>
              <div className={styles.plan}>
                <input
                  type="radio"
                  name="plan"
                  id="19.99"
                  value="$19.99"
                  onChange={handleInputChange}
                  disabled
                />
                <label htmlFor="19.99">
                  <strike>$19.99</strike>
                </label>
              </div>
            </form>
            <Elements stripe={stripePromise}>
              <Payment
                plan={plan}
                setIsPaymentComplete={setIsPaymentComplete}
              />
            </Elements>
          </div>
        </div>
      </div>

      <Adsense />
    </div>
  );
}

export default Icecream;
