import Button from "../button/button";
import Tweet from "./tweet";
import styles from "./tweets.module.css";
import { Spinner } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { useState } from "react";
import CheckInfluencer from "../check-influencer/checkInfluencer";
import influencerData from "../../localData/influencerData.json";
import ReactGa from "react-ga";
import Adsense from "../googld-ad/Adsense";
import SearchBox from "../search-box/SearchBox";

function Tweets({ twitter }) {
  const [tweetData, setTweetData] = useState(null);
  const [progress, setProgress] = useState(undefined);
  const [checkedUsernameList, setCheckedUsernameList] = useState([]);
  const [queryLimit, setQueryLimit] = useState(false);
  const [isResponseEmpty, setIsResponseEmpty] = useState(false);
  const [searchKeyword, setSearchKeyword] = useState(undefined);
  async function handlePullData(event) {
    event.preventDefault();
    ReactGa.event({
      category: "Pull Data Button",
      action: "Click Pull Data button from NFT Tweet page",
    });
    setProgress("on");
    const data = await twitter.getTweets(checkedUsernameList, searchKeyword);
    if (!data) {
      setTweetData(null);
      setProgress(undefined);
      setIsResponseEmpty(true);
      return;
    }
    setTweetData({ tweets: data.tweets, users: data.users });
    setProgress(undefined);
    setIsResponseEmpty(false);
  }

  const influencerNameList = Object.keys(influencerData);

  const checkQueryLimit = (authorsArray) => {
    let resultStrings = "from:";
    for (const author of authorsArray) {
      if (resultStrings.length === 5) {
        resultStrings += author;
      } else {
        let fromSecondAuthor = ` OR from:${author}`;
        resultStrings += fromSecondAuthor;
      }
    }
    return resultStrings.length;
  };

  const addAuthor = (name) => {
    let length;
    if (searchKeyword) {
      length =
        checkQueryLimit([...checkedUsernameList, influencerData[name]]) +
        searchKeyword.length;
    } else {
      length = checkQueryLimit([...checkedUsernameList, influencerData[name]]);
    }
    if (length < 487) {
      setQueryLimit(false);
    } else {
      setQueryLimit(true);
    }
    setCheckedUsernameList([...checkedUsernameList, influencerData[name]]);
  };
  const deleteAuthor = (name) => {
    const updatedItems = checkedUsernameList.filter(
      (element) => element !== influencerData[name]
    );
    let delLength;
    if (searchKeyword) {
      delLength = checkQueryLimit(updatedItems) + searchKeyword.length;
    } else {
      delLength = checkQueryLimit(updatedItems);
    }
    if (delLength < 487) {
      setQueryLimit(false);
    } else {
      setQueryLimit(true);
    }
    setCheckedUsernameList(updatedItems);
  };

  const onChangeSearchBox = (event) => {
    const keyword = event.target.value;
    setSearchKeyword(keyword);
    const searchLength =
      checkQueryLimit([...checkedUsernameList]) + keyword.length;

    if (searchLength < 487) {
      setQueryLimit(false);
    } else {
      setQueryLimit(true);
    }
  };

  return (
    <div className={styles.body}>
      <article className={styles.article}>
        Influencers' Tweets created in the last 7 days.
      </article>
      <form onSubmit={handlePullData}>
        <div className={styles.checkInfluencerContainer}>
          {influencerNameList.map((name) => (
            <CheckInfluencer
              key={name}
              name={name}
              addAuthor={addAuthor}
              deleteAuthor={deleteAuthor}
            />
          ))}
        </div>
        {queryLimit ? (
          <div className={styles.queryLimit}>
            Query length limit is exceeded!
          </div>
        ) : null}
        {!checkedUsernameList.length ? (
          <div className={styles.mustKeyword}>
            <span className={styles.warning}>
              <i className="fas fa-exclamation-triangle"></i>
            </span>
            NFT Influencer must be selected
          </div>
        ) : null}
        {!searchKeyword ? (
          <div className={styles.mustKeyword}>
            <span className={styles.warning}>
              <i className="fas fa-exclamation-triangle"></i>
            </span>
            Search keyword must be entered
          </div>
        ) : null}

        <div className={styles.searchBox}>
          <SearchBox onChangeSearchBox={onChangeSearchBox} />
        </div>
        <div className={styles.buttonContainer}>
          {progress === "on" ? (
            <Spinner
              className={styles.spinner}
              animation="grow"
              variant="light"
            />
          ) : (
            <Button
              type="submit"
              disabled={
                queryLimit || !searchKeyword || !checkedUsernameList.length
              }
              text={"PULL DATA"}
            />
          )}
        </div>
      </form>

      <div className={styles.spinner}>
        <div className={styles.tweetListBox}>
          {isResponseEmpty && "No data exist!"}
          {tweetData &&
            tweetData.tweets.map((data) => (
              <Tweet key={data.id} data={data} users={tweetData.users} />
            ))}
        </div>
      </div>
      <Adsense />
    </div>
  );
}

export default Tweets;
